    #t_3_3.titulo-segundo
      h2.mt-5 3.3 Acciones de Gestión


    h3.mt-5 Poblacionales
    p Los procesos de gestión de la Salud Pública definidos en la resolución 518 de 2015 definen una serie de acciones que permiten la afectación de los determinantes sociales de la salud y el logro de los resultados en salud desde la gestión sectorial, intersectorial y comunitaria, en tanto la apuesta son los resultados en salud - vía la gestión de atenciones - que requiere unos procesos de gestión.

    figure.p-3.my-5
      img(src='@/assets/curso/img29.svg', style="width:950px").m-auto

    p Son en total 13 procesos, entre los cuales se encuentran los misionales (que solo desarrolla el sector salud), estratégicos (que son desarrollados desde otros sectores, con particularidades para el sector y desde donde se potencia la interlocución con otros actores y sectores) y finalmente los procesos de apoyo. Es importante que dependiendo del proceso, la función y el relativo liderazgo de los actores cambia, aunque se mantiene la participación directa o indirecta; por tanto para la planeación integral en salud el liderazgo le corresponde a la entidad territorial de salud, mientras que en el caso de la gestión de intervenciones individuales, el asegurador con su red de prestación de servicios son los  actores principales del proceso.
      br
      br
      |La articulación de los procesos y el desarrollo de las responsabilidades de los actores, es esencial para lograr brindar las intervenciones en salud definidas en la RPMS, considerando además, que el proceso de gestión de las intervenciones individuales (con participación clave de los aseguradores y su red de prestación de servicios) aporta directamente a la garantía de dichas atenciones en términos de oportunidad, disponibilidad, continuidad e integralidad.  

